.form_area{
    max-width: 300px;
    margin: 50px auto;
    background-color: #008B8B;
    padding: 20px;
    font-family: Arial, serif;
    border-radius: 10px;
    position: relative;
}

.input-section{
    position: relative;
    padding: 5px 0 10px;
}

label{
    position: absolute;
    top: -5px;
    left: 0;
    color: #ffffff;
    font-size: 15px;
}

.inp{
    display: block;
    margin: 10px 0;
    width: 100%;
    height: 30px;
    font-size: 20px;
    border-radius: 5px;
    outline: none;
    background-color: #ffffff;
    border: 2px solid #ffffff;
}

.inp:focus{
    border-color: #142850;
}

.submit{
    width: 100%;
    margin-top: 10px;
    height: 38px;
    border-radius: 5px;
    outline: none;
    font-size: 20px;
    background-color: #ffffff;
    color: #142850;
    border: 1px solid #ffffff;
}

.submit:active{
    transform: translate(2px, 2px);
}

.submit:focus{
    border: 1px solid #ffffff;
    box-shadow: none;
}

.submit:hover{
    cursor: pointer;
    background-color: #008B8B;
    border: 1px solid #494343;
    box-shadow: 0 0 2px 3px #494343;
    color: #ffffff;
}

.error{
    position: absolute;
    bottom: 0;
    right: 0;
    color: #af0606;
    font-size: 15px;
}
